  methods: {
    ShowOverlay(img_src) {
      let searched = this.img_list.map(obj => {
        if (Object.keys(obj).some(key => obj[key].indexOf(img_src) != -1))
          return obj;
      });
      this.show_img = this.big_list[this.img_list.indexOf(searched.filter(obj => obj != undefined)[0])].src;
      this.ov = !this.ov;
    },
  },
  computed: {
    filter_list() {
      if (this.imgtag == 0) return this.img_list;
      else return this.img_list.filter(img => img.tag == this.tags[this.imgtag]);
    }
